import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  inject,
} from '@angular/core';
import { cl } from '@fullcalendar/core/internal-common';
import { AutoCompleteCompleteEvent } from 'primeng/autocomplete';
import { Subscription } from 'rxjs';
import {
  Mercancia,
  MercanciaPorUbi,
} from 'src/app/interfaces/mercancia.interface';
import { LineaTarea } from 'src/app/interfaces/tarea.interface';
import { Ubicacion } from 'src/app/interfaces/ubicaciones.interface';
import { MercanciaService } from 'src/app/services/mercancia.service';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { TareaService } from 'src/app/services/tarea.service';
import { UbicacionesService } from 'src/app/services/ubicaciones.service';
import { ActivatedRoute, Router } from '@angular/router';
import { OrdenesService } from 'src/app/services/ordenes.service';

@Component({
  selector: 'app-mostrar-tarea-pre-rec',
  templateUrl: './mostrar-tarea-pre-rec.component.html',
  styleUrls: ['./mostrar-tarea-pre-rec.component.scss'],
})
export class MostrarTareaPreRecComponent implements OnDestroy, OnInit {
  @Input() tarea: LineaTarea;
  @Output() actualizar = new EventEmitter<boolean>();

  private readonly ubicacionesService = inject(UbicacionesService);
  private readonly tareaService = inject(TareaService);
  private readonly mercanciaService = inject(MercanciaService);
  private readonly messageService = inject(CustomMessageService);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly router = inject(Router);

  ubicaciones: Ubicacion[] = [];
  mercancias: MercanciaPorUbi[] = [];

  cantidad: number = null;

  barcode: string = '';
  mercanciaSeleccionada: MercanciaPorUbi = null;

  ubicacionAntiguaSeleccionada: Ubicacion = null;
  ubicacionNuevaSeleccionada: Ubicacion = null;

  public codeInputLength: number = 0;
  public soloNumeros: boolean;
  public codigoInvalido: boolean = false;
  public cantidadInvalida: boolean = false;

  public codigoEan: string;

  public ubicacionAntigua: string = null;
  public ubicacionNueva: string = null;

  public subs: Subscription[] = [];

  public cantidadExigidad = -1;

  public ubicacionInvalida: boolean = false;
  public ubicacionInvalidaDestino: boolean = false;

  public validarTodoV: boolean;

  public ordenID: string = null;
  public tareaActualID: number = null;
  public tareaSig: number = null;

  @Input() loading = false;

  ngOnInit(): void {
    console.log(this.tarea);
    this.cantidadExigidad = parseInt(this.tarea.ref2);
    this.subs.push(
      this.activatedRoute.params.subscribe({
        next: async (params) => {
          this.ordenID = await params['idOrden'];
          this.tareaActualID = await params['id'];
          this.cargarTareaSig();
        },
      })
    );

    if (this.tarea.preparaciones.tipo === 'Recurso') {
      this.cargarUbicaciones();
    }
  }

  back() {
    let lastUrl = sessionStorage.getItem('lastUrl');
    this.router.navigateByUrl(lastUrl);
  }

  cargarUbicaciones() {
    this.subs.push(
      this.ubicacionesService.getUbicaciones().subscribe({
        next: (res) => {
          console.log('UBICACIONES =>', res);
          this.ubicaciones = res;
        },
        error: (error) => {
          console.error(error);
          this.messageService.error(error.error.detail);
        },
      })
    );
  }

  cargarTareaSig() {
    this.tareaSig = null;
    this.subs.push(
      this.tareaService.getOrdenTrabajo(this.ordenID).subscribe({
        next: (res) => {
          // console.log(res);
          res.lineasTareas.forEach((lineaTarea) => {
            if (
              this.tareaSig == null &&
              lineaTarea.id != this.tareaActualID &&
              lineaTarea.estado !== 'finalizado' &&
              (lineaTarea.tipo === 'pre_rec' ||
                lineaTarea.tipo === 'movimiento')
            ) {
              this.tareaSig = lineaTarea.id;
              console.log('DE => ', res.lineasTareas);
              console.log('AGARRE => ', lineaTarea.id);
            }
          });
        },
        error: (error) => {
          console.error(error);
          this.messageService.error(error.error.detail);
        },
      })
    );
  }

  siguienteTarea() {
    if (this.tareaSig == null) {
      this.back();
    } else {
      this.router.navigateByUrl('/tarea/' + this.tareaSig + '/' + this.ordenID);
      this.actualizar.emit();
    }
  }

  focusCantidad() {
    setTimeout(() => {
      document.getElementById('cantidad').focus();
      //@ts-ignore
      document.getElementById('cantidad').value = null;
    }, 100);
  }

  focusBarcode() {
    setTimeout(() => {
      document.getElementById('barcode').focus();
      //@ts-ignore
      document.getElementById('barcode').value = null;
    }, 100);
  }

  // validarTodo() {
  //   if (this.tarea.estado === 'iniciado') {
  //     return (
  //       !this.ubicacionInvalida && this.codigoInvalido && !this.cantidadInvalida
  //     );
  //   } else if (this.tarea.estado === 'proceso') {
  //     return !this.ubicacionInvalida;
  //   }

  //   return false;
  // }

  validarUbicaciones() {
    // si la ubicacion ingresada es vacio o null es invalido
    if (this.ubicacionAntigua === null || this.ubicacionAntigua === '') {
      this.ubicacionInvalida = true;
      return false;
    }

    // si estamos en un recurso y tenemos ubicaciones
    // esta condicion se salta si no tenemos ubicaiones por lo que no estamos en un recurso
    // if (this.ubicaciones.length > 0) {
    //   const existe = this.ubicaciones.find(
    //     (ubi) => ubi.ubicacionId === this.ubicacionAntigua
    //   );
    //   // verificamos si la ubicacion ingresada existe y retornamos valido si es asi
    //   if (existe) {
    //     this.ubicacionInvalida = false;
    //     return true;
    //   } else {
    //     this.ubicacionInvalida = true;
    //     return false;
    //   }
    // }

    // si la ubicacion de origen ingresada es igual a la solicitada es valido
    if (this.ubicacionAntigua === this.tarea.preparaciones?.origen) {
      this.ubicacionInvalida = false;
      return true;
    } else {
      this.ubicacionInvalida = true;
      return false;
    }
  }

  validarUbicacionesDestino() {
    if (this.ubicacionNueva !== null && this.ubicacionNueva !== '') {
      if (this.ubicacionNueva === this.tarea.preparaciones.destino) {
        this.ubicacionInvalidaDestino = false;
        return true;
      } else {
        this.ubicacionInvalidaDestino = true;
        return false;
      }
    } else {
      this.ubicacionInvalidaDestino = true;
      return false;
    }
  }

  validarCodigo() {
    // this.codeInputLength = this.barcode.length;

    // const regex = /^[0-9]*$/;
    // this.soloNumeros = regex.test(this.barcode);

    if (
      this.barcode !== null &&
      this.barcode !== undefined &&
      this.barcode !== '' &&
      this.barcode == this.tarea.ref1
    ) {
      this.codigoInvalido = false;
      return true;
    } else {
      this.codigoInvalido = true;
      return false;
    }
  }

  validarCantidad() {
    if (
      this.cantidad !== null &&
      this.cantidad > 0 &&
      this.cantidad == parseInt(this.tarea.ref2)
    ) {
      this.cantidadInvalida = false;
      return true;
    }
    this.cantidadInvalida = true;
    return false;
  }

  submit(sig: boolean = false) {
    this.validarTodoV = true;
    if (!this.validarUbicaciones()) {
      this.validarTodoV = false;
    }
    if (!this.validarCodigo()) {
      this.validarTodoV = false;
    }
    if (!this.validarCantidad()) {
      this.validarTodoV = false;
    }

    if (!this.validarTodoV) return;

    const body = {
      id: this.tarea.id,
      codigoEan: this.barcode,
      origen: this.ubicacionAntigua,
      cantidad: this.cantidad,
    };

    console.log(body);

    this.loading = true;

    this.subs.push(
      this.tareaService.registrarPreRecParte1(body).subscribe({
        next: (res) => {
          console.log(res);
          this.messageService.add({
            summary: 'Exito',
            severity: 'success',
            detail: 'Registra de Movimiento realizado con exito',
          });
          this.ubicacionAntigua = null;
          this.barcode = null;
          this.cantidad = null;
          if (sig) {
            // this.siguienteTarea()
            this.back();
          } else {
            this.actualizar.emit(true);
          }
        },
        error: (error) => {
          this.loading = false;
          console.error(error);
          error.error?.detail
            ? this.messageService.error(error.error.detail)
            : error.error.message == 'JWT Token not found'
            ? this.messageService.alert('Sesion Expirada')
            : this.messageService.error(
                'Ocurrio un error desconocido en local'
              );
        },
      })
    );
  }

  submitProceso(sig: boolean = false) {
    this.validarTodoV = true;
    if (!this.validarUbicacionesDestino()) {
      this.validarTodoV = false;
    }

    if (!this.validarTodoV) return;

    const body = {
      id: this.tarea.id,
      destino: this.ubicacionNueva,
    };

    console.log(body);

    this.loading = true;

    this.subs.push(
      this.tareaService.registrarPreRecParte2(body).subscribe({
        next: (res) => {
          console.log(res);
          this.messageService.add({
            summary: 'Exito',
            severity: 'success',
            detail: 'Registra de Movimiento realizado con exito',
          });
          this.ubicacionNueva = null;
          if (sig) {
            // this.siguienteTarea()
            this.back();
          } else {
            this.back();
          }
        },
        error: (error) => {
          console.error(error);
          this.loading = false;
          error.error?.detail
            ? this.messageService.error(error.error.detail)
            : error.error.message == 'JWT Token not found'
            ? this.messageService.alert('Sesion Expirada')
            : this.messageService.error(
                'Ocurrio un error desconocido en local'
              );
        },
      })
    );
  }

  ngOnDestroy(): void {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
